html {
  box-sizing: border-box;
  font-size: 16px;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  background-color: #a8a7ff;
  user-select: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body,
h1,
h2,
h3,
h4,
p,
ol,
ul,
input,
textarea {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}
.container {
  display: block;
  max-width: 480px;
  margin: 0 auto;
  padding: 1rem;
}
.main {
  background-color: #fff;
  box-shadow: 0 1px 4px black;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input,
select,
textarea {
  border: none;
  outline: none;
}

::placeholder {
  color: rgba(0, 0, 0, 0.2);
}

.title {
  color: #565581;
  font-size: 64px;
  font-weight: 500;
  padding-bottom: 20px;
  text-align: center;
}
